import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Cardd from './Cardd'
import { Spinner, Button } from 'react-bootstrap'
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'


function Input(props) {
  const [query, setQuery] = useState('')
  const [baseUrl, setBaseUrl] = useState(`https://api.themoviedb.org/3/discover/movie?api_key=872bb5be5fb02a7c99c5227bc9647d70&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=1`)
  const [data, setData] = useState({ results: [] })
  const [isLoading, setIsLoading] = useState(false)
  const [open, setOpen] = useState(false)



  useEffect(() => {
    const rand = () => {
      return Math.floor(Math.random() * 20)
    }
    const fetchData = async () => {
      setIsLoading(true)

      const result = await axios(baseUrl)
      const { results } = result.data
      setData({ results: results })


      setIsLoading(false)
    }
    console.log(props)
    fetchData()
  }, [baseUrl])



  return (
    <>

      {/* <input type="text" onChange={(event)=>setQuery(event.target.value)}/> */}
      <Container fluid>
        <Row>
          <Col sm={{ offset: 4, span: 4 }}> <InputGroup style={{ marginTop: '2px' }} className="sm-3" onChange={(event) => setQuery(event.target.value)}>
            <FormControl placeholder="Type in movie to search for" value={query} onKeyPress={(event) => {
              {if(query ==='') { return}}
              
              if (event.key === 'Enter') { 
              setBaseUrl(`https://api.themoviedb.org/3/search/movie?api_key=872bb5be5fb02a7c99c5227bc9647d70&language=en-US&query=${query}&page=1`) } }} />
            <Button disabled={isLoading || query === ''} style={{ margin: "2px", marginTop: '2px', padding: '8px' }} variant="info" onClick={!isLoading ? () => setBaseUrl(`https://api.themoviedb.org/3/search/movie?api_key=872bb5be5fb02a7c99c5227bc9647d70&language=en-US&query=${query}&page=1`) : null}> {isLoading ? 'Loading…' : 'Search'}</Button>
          </InputGroup>
            <p style={{ fontSize: '15px' }}>Need a hint? Try to type in<strong> <a onClick={() => setQuery('Avengers')}>Avengers</a></strong> </p>

          </Col>
        </Row>
      </Container>



      {isLoading ? <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}> <Spinner animation="border" role="status">
        <span className="sr-only">Loading...</span>
      </Spinner></div> :

        <div style={{ display: "flex", flexWrap: "wrap", justifyContent: "center" }}>
          {data.results.map(item => (
            <Cardd title={item.title} overview={`${item.overview.substring(0, 200)}...`} poster_path={item.poster_path}
            />

          ))}
        </div>}

    </>
  )
}

export default Input



{/* <ul>
                {data.results.map(item => (
                  <li key={item.id}>
                        <h1>{item.title}</h1>
                  </li>
                ))}
              </ul>*/}