import React, { useState, useEffect } from 'react'
import { Card, Button, Image } from 'react-bootstrap'
import Input from './Input'

function Cardd(props) {
  return <Card style={{ width: '18rem', margin: '3rem', overflow: 'hidden' }}>
    <Card.Img variant="top" src={`https://image.tmdb.org/t/p/w185_and_h278_bestv2${props.poster_path}`} />
    <Card.Body>
      <Card.Title>{props.title}</Card.Title>
      <Card.Text>
        {props.overview}
      </Card.Text>
      <Button
        variant="light">button</Button>
    </Card.Body>
  </Card>
}


export default Cardd 