import React from 'react'
import Navbar from 'react-bootstrap/Navbar'


function Nav() {
    return <>
        <Navbar bg="light">
            <Navbar.Brand href="#home">
                <img
                    src={require('./react.png')}
                    width="30"
                    height="30"
                    className="d-inline-block align-top"
                    alt="React Bootstrap logo"
                />
                {' '}
                React Project
    </Navbar.Brand>
        </Navbar>

    </>
}

export default Nav 